import { Injectable } from '@angular/core';
import {
  HttpClient,
  HttpParams,
  HttpHeaders,
  HttpErrorResponse,
  HttpResponse,
} from '@angular/common/http';
import { Observable, throwError, forkJoin } from 'rxjs';
import { filter, catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { MercueResponse } from 'src/app/model/mercue-response.class';
import { API, COMMON } from 'src/app/const/global-constants.const';
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root',
})
export class MercueRequestService {
  /**
   * 是否已顯示無效訊息
   * - 用於判斷僅需彈跳一次
   */
  isPopupInvalidMsg = false;
  constructor(
    private http: HttpClient,
    private router: Router,

  ) { }

  /**
   * call HTTP 主要方法
   * @param method http method
   * @param requestParams request body
   * @param url api url
   */
  request(method: string, requestParams: any, url: string): Observable<MercueResponse> {
    const httpHeaders = this.getHTTPHeaders();
    if (API.POST === method) {
      return this.http
        .post<any>(
          environment.DEFAULT_IP  + url, requestParams, { headers: httpHeaders }
        )
        .pipe(filter(this.handleResponse), catchError(this.handleError));
    } else {
      return this.http
        .get<any>(environment.DEFAULT_IP + API.HREF + url, { headers: httpHeaders, params: requestParams })
        .pipe(filter(this.handleResponse), catchError(this.handleError));
    }
  }




  /**
   * 繞過 spring security 直接回驗
   * @param url api url
   */
  requestSsoServer(url: string): Observable<any> {
    const httpHeaders = new HttpHeaders({
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + sessionStorage.getItem(COMMON.SSO_TOKEN)
    });
    return this.http
      .get<any>(sessionStorage.getItem('ssoServerUrl') + url, { headers: httpHeaders })
      .pipe(filter(this.handleQueryProgressResponse), catchError(this.handleError));
  }

  /**
   * 取得json file
   * @param url file 路徑
   */
  getJSONfile(url: string): Observable<any> {
    return this.http.get(url);
  }



  private getHTTPHeaders(): HttpHeaders {
    const result = new HttpHeaders({
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + sessionStorage.getItem(COMMON.TOKEN)
    });
    return result;
  }

  private getQueryProgressHTTPHeaders(): HttpHeaders {
    const result = new HttpHeaders({
      Accept: 'application/json',
      'Content-Type': 'application/json',
    });
    return result;
  }

  /**
   * for Status Code 200的情況 (response error) && backend throw exception
   * @param response : MercueResponse
   */
  handleResponse = (response: MercueResponse): boolean => {
    let hasResult = true;

    return hasResult;
  }

  /**
   * for Status Code 200的情況 (response error) && backend throw exception
   * @param response : MercueResponse
   */
  handleQueryProgressResponse = (response: MercueResponse): boolean => {
    let hasResult = true;
    if (!response) {

      hasResult = false;
    }
    return hasResult;
  }

  handleError = (error: HttpErrorResponse) => {
    console.log('handleError');

    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` + `body was: ${error.error}`
      );
    }
    // return an observable with a user-facing error message
    return throwError('Something bad happened; please try again later.');
  }

}


