import { CatViewComponent } from './cat-view/cat-view.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import{FlowComponent}from'src/app/flow/flow.component';
import { BonusComponent } from './bonus/bonus.component';
import { BonusDetailComponent } from './bonus-detail/bonus-detail.component';
const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'flow'
  },
  {
    path: 'flow',
    component: FlowComponent
  },
  {
    path: 'cat',
    component: CatViewComponent
  },
  {
    path: 'bonus',
    component: BonusComponent
  },
  {
    path: 'bonus-detail',
    component: BonusDetailComponent
  },
  {
    path: '**',
    redirectTo: 'flow'
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
