import { Loop } from './../../../node_modules/@babel/types/lib/index-legacy.d';
import { Component, NgZone, OnInit } from '@angular/core';
import { AnimationItem } from 'lottie-web';
import { AnimationOptions } from 'ngx-lottie';
import * as $ from 'jquery';
@Component({
  selector: 'app-cat-view',
  templateUrl: './cat-view.component.html',
  styleUrls: ['./cat-view.component.scss']
})
export class CatViewComponent implements OnInit {

  defaultOptions: AnimationOptions = {
    path: './assets/image/defaultCat.json',
  };

  happyOtions: AnimationOptions = {
    path: './assets/image/FeedCatHappyEat.json',
  };

  styles: Partial<CSSStyleDeclaration> = {
    maxWidth: '500px',
    margin: '0 auto',
  };

  /** 貓幣 */
  coin = 50;

  private defaultAnimationItem!: AnimationItem;
  private happyAnimationItem!: AnimationItem;

  constructor(
    private ngZone: NgZone
  ) { }

  ngOnInit(): void {
  }

  defaultAnimationCreated(animationItem: AnimationItem): void {
    animationItem.autoplay = false;
    this.defaultAnimationItem = animationItem;
  }

  happyAnimationCreated(animationItem: AnimationItem): void {
    animationItem.autoplay = false;
    this.happyAnimationItem = animationItem;
  }

  onDefaultLoopComplete(): void {
    console.log('default ok');
    NgZone.assertNotInAngularZone();
    this.defaultAnimationItem.stop();
  }

  onHappyLoopComplete(): void {
    console.log('happy ok');
    NgZone.assertNotInAngularZone();
    this.happyAnimationItem.stop();
    $('.cat-feed').fadeIn('3000');
  }

  // updateAnimation(): void {
  //   // ⚠️⚠️ Don't do this!
  //   this.options.assetsPath = 'https://assets5.lottiefiles.com/packages/lf20_zzytykf2.json';

  //   // ✔️✔️ Update `options` in this way
  //   this.options = {
  //     ...this.options, // In case you have other properties that you want to copy
  //     path: 'https://assets5.lottiefiles.com/packages/lf20_zzytykf2.json',
  //   };
  // }

  onFeedClick(): void {
    // $('#feedModal').show();
  }

  play(): void {
    // $('#feedModal').hide();
    //  有錢餵貓貓
    if (this.coin > 0) {
      $('.cat-feed').fadeOut('3000');
      this.happyAnimationItem.play();

      setTimeout(() => {
        this.coin -= 50;
      }, 3200);
    } else {
      // 沒錢餵貓，貓哭哭
      this.defaultAnimationItem.play();

      $('#content').fadeIn('3000');
      setTimeout(() => {
        $('#content').fadeOut('3000');
      }, 2000);
    }




    // this.ngZone.runOutsideAngular(() => {

    // });
  }
}
